import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'AntiqueSwap',
  description:
    'The most popular AMM on BSC by user count! Earn AQE through yield farming or win it in the Lottery, then stake it in Syrup Pools to earn more tokens! Initial Farm Offerings (new token launch model pioneered by AntiqueSwap), NFTs, and more, on a platform you can trust.',
  image: '',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  let basePath
  if (path.startsWith('/swap')) {
    basePath = '/swap'
  } else if (path.startsWith('/add')) {
    basePath = '/add'
  } else if (path.startsWith('/remove')) {
    basePath = '/remove'
  } else if (path.startsWith('/teams')) {
    basePath = '/teams'
  } else if (path.startsWith('/voting/proposal') && path !== '/voting/proposal/create') {
    basePath = '/voting/proposal'
  } else {
    basePath = path
  }

  switch (basePath) {
    case '/':
      return {
        title: `${t('Home')} | ${t('AntiqueSwap')}`,
      }
    case '/swap':
      return {
        title: `${t('Exchange')} | ${t('AntiqueSwap')}`,
      }
    case '/add':
      return {
        title: `${t('Add Liquidity')} | ${t('AntiqueSwap')}`,
      }
    case '/remove':
      return {
        title: `${t('Remove Liquidity')} | ${t('AntiqueSwap')}`,
      }
    case '/liquidity':
      return {
        title: `${t('Liquidity')} | ${t('AntiqueSwap')}`,
      }
    case '/find':
      return {
        title: `${t('Import Pool')} | ${t('AntiqueSwap')}`,
      }
    case '/competition':
      return {
        title: `${t('Trading Battle')} | ${t('AntiqueSwap')}`,
      }
    case '/prediction':
      return {
        title: `${t('Prediction')} | ${t('AntiqueSwap')}`,
      }
    case '/prediction/leaderboard':
      return {
        title: `${t('Leaderboard')} | ${t('AntiqueSwap')}`,
      }
    case '/farms':
      return {
        title: `${t('Farms')} | ${t('AntiqueSwap')}`,
      }
    case '/farms/auction':
      return {
        title: `${t('Farm Auctions')} | ${t('AntiqueSwap')}`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | ${t('AntiqueSwap')}`,
      }
    case '/lottery':
      return {
        title: `${t('Lottery')} | ${t('AntiqueSwap')}`,
      }
    case '/collectibles':
      return {
        title: `${t('Collectibles')} | ${t('AntiqueSwap')}`,
      }
    case '/ifo':
      return {
        title: `${t('Initial Farm Offering')} | ${t('AntiqueSwap')}`,
      }
    case '/teams':
      return {
        title: `${t('Leaderboard')} | ${t('AntiqueSwap')}`,
      }
    case '/profile':
      return {
        title: `${t('Your Profile')} | ${t('AntiqueSwap')}`,
      }
    case '/profile/tasks':
      return {
        title: `${t('Task Center')} | ${t('AntiqueSwap')}`,
      }
    case '/voting':
      return {
        title: `${t('Voting')} | ${t('AntiqueSwap')}`,
      }
    case '/voting/proposal':
      return {
        title: `${t('Proposals')} | ${t('AntiqueSwap')}`,
      }
    case '/voting/proposal/create':
      return {
        title: `${t('Make a Proposal')} | ${t('AntiqueSwap')}`,
      }
    case '/info':
      return {
        title: `${t('Overview')} | ${t('SALIX ( SLX ) Info & Analytics')}`,
        description: 'View statistics for AntiqueSwap exchanges.',
      }
    case '/info/pools':
      return {
        title: `${t('Pools')} | ${t('SALIX ( SLX ) Info & Analytics')}`,
        description: 'View statistics for AntiqueSwap exchanges.',
      }
    case '/info/tokens':
      return {
        title: `${t('Pools')} | ${t('SALIX ( SLX ) Info & Analytics')}`,
        description: 'View statistics for AntiqueSwap exchanges.',
      }
    default:
      return null
  }
}
